
    import {defineComponent, computed,ref} from 'vue';
    import {useStore} from 'vuex';
    export default defineComponent({
        name: "Aside",
        setup(){
            const store = useStore();
            let leftMenu = computed(()=>{
                return store.state.app.leftMenu
            })
            return {
                leftMenu
            }
        }
    })
